/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
}
-webkit-text-size-adjust 100%,
-ms-text-size-adjust 100% {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #3f4040;
  font: normal 1.6rem/1.5 'Rubik';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #0098dc;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  text-align: center;
}
.header__target {
  font-size: 1.8rem;
  text-transform: uppercase;
  border-bottom: 6px solid #dfdddb;
  padding: 15px 0 10px;
}
.header__target span {
  display: block;
  margin-top: 10px;
}
.header__logo {
  margin: 20px 0;
}
.header__logo img {
  max-width: 222px;
}
.header__logo-slogan {
  font-size: 1.8rem;
  margin-top: 5px;
}
.header__contacts {
  font-size: 1.8rem;
  border-bottom: 6px solid #dfdddb;
  padding-bottom: 15px;
}
.header__contacts-phone {
  color: #3f4040;
  font-weight: bold;
  font-size: 2rem;
  text-decoration: none;
}
.header__contacts-workhours {
  font-weight: 300;
}
.header__contacts-links {
  font-weight: 500;
  margin-top: 5px;
}
.header__contacts-links a {
  color: #38ae25;
  text-decoration: underline;
}
.header__contacts-links a:last-child {
  display: none;
}
.header__contacts-links a:hover {
  text-decoration: none;
}
.header__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
.header__action-item {
  position: relative;
  color: #3f4040;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
.header__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #e85c10;
  content: '';
  transition: all 0.2s;
}
a:hover.header__action-item {
  text-decoration: none;
}
a:hover.header__action-item::after {
  width: 100%;
}
.header__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #3f4040;
  transform: translateX(-50%);
  transition: all 0.2s;
}
a:hover.header__action-item {
  color: #e85c10;
}
a:hover.header__action-item svg {
  fill: #e85c10;
}
.header__action-item:last-child {
  margin-bottom: 0;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
  background-color: #38ae25;
}
.nav__main {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}
.nav__item {
  width: 55%;
  color: #fff;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-top: 20px;
}
.nav__item:nth-child(even) {
  width: 45%;
}
.nav__item--arrow {
  display: inline-flex;
  align-items: center;
}
.nav__item--arrow::after {
  width: 13px;
  height: 13px;
  border: solid #fff;
  border-width: 0 0 2px 2px;
  margin-left: 10px;
  transform: translateY(calc(-50% + 1px)) rotate(-45deg);
  content: '';
}
/*  </nav>  */
/*  <footer>  */
.footer {
  flex-shrink: 0;
  background-color: #242424;
}
.footer__main {
  color: #fff;
  text-align: center;
  margin: 40px 0 35px;
}
.footer__slogan {
  font-size: 1.8rem;
  margin: 15px 0 25px;
}
.footer__slogan span {
  display: block;
}
.footer__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
.footer__action-item {
  position: relative;
  color: #fff;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
.footer__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #e85c10;
  content: '';
  transition: all 0.2s;
}
a:hover.footer__action-item {
  text-decoration: none;
}
a:hover.footer__action-item::after {
  width: 100%;
}
.footer__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #fff;
  transform: translateX(-50%);
  transition: all 0.2s;
}
a:hover.footer__action-item {
  color: #ed8223;
}
a:hover.footer__action-item svg {
  fill: #ed8223;
}
.footer__action-item:last-child {
  margin-bottom: 0;
}
.footer__copyright {
  font-size: 1.8rem;
  margin: 25px 0;
}
.footer__copyright p {
  display: inline;
}
.footer__privacy {
  font-weight: 300;
  font-size: 1.6rem;
  padding: 0 30px;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.8rem;
  margin: 30px 0;
}
.footer__nav-title {
  color: #0098dc;
  font-weight: 500;
  margin-bottom: 5px;
}
.footer__nav-item {
  color: #fff;
  margin: 5px 0;
}
.footer__contacts {
  font-size: 1.8rem;
  border-top: 1px solid #121212;
  padding: 25px 15px 0;
  margin: 0 -15px;
}
.footer__phone-number {
  color: #ed8223;
  font-weight: 500;
  font-size: 2.4rem;
}
.footer__workhours {
  margin: 5px 0 15px;
}
.footer__workhours p {
  display: inline;
}
.footer__address-title {
  font-weight: bold;
  padding: 0 40px;
}
.footer__address-main {
  font-style: normal;
  margin-top: 10px;
}
.footer__email {
  margin-top: 15px;
}
.footer__email-main {
  color: #ed8223;
  font-weight: bold;
}
/*  </footer>  */
